button{
    height: 80px;
    width: 80px;
    color: aliceblue;
    background-color: rgb(232, 61, 161);
    text-decoration: solid;
    border-radius: 20px;
    border-color: white;
    border-style: solid;
    margin-bottom: 5px;
    margin-top: 0px;
    margin-right: 12px;
    font-size: large;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 100px;
    font-style: bold;
}
button:hover{
    color: black;
    background-color: aqua;
}
.res{
    color: white;
    background-color: rgb(201, 9, 147);
    width: 350px;
    margin-top: 30px;
    margin-left: 12px;
}
.min{
    display: inline;
    margin-left: 20px;
}
#box{
    height: 700px;
    width: 400px;
    color: aliceblue;
    background-color: rgb(237, 136, 195);
    margin-top: 30px;
    margin-left: 520px;
    border-radius: 10px;
    border-color: black;
    border-style: solid;
    padding: 10px;
}
input{
    color: rgb(6, 6, 232);
    border-color: white;
    border-radius: 20px;
    height: 50px;
    width: 350px;
    margin-top: 20px;
    margin-bottom: 50px;
    font-size: large;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 100px;
    font-style: bold;
    text-align: center;
}
p{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: black;
    font-style: bold;
}
.pl{
    height: 165px;
    float: right;
    margin-left: 0px;
    margin-right: 20px;
}
.im{
    background-image:url('https://wallpapercave.com/wp/wp2581374.jpg');
}
.ze{
    width:175px;
}
.fl{
    float:left;
    display: inline;
    margin-left: 20px;
}
::placeholder { 
    color: red;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-style: bold;
    font-size: 20px;
    opacity:1;
  }